}

*::-webkit-scrollbar-track {
  border-radius: 5px;
  background-color: #ffffff;
}

*::-webkit-scrollbar-track:hover {
  background-color: #ffffff;
}

*::-webkit-scrollbar-track:active {
  background-color: #ffffff;
}

*::-webkit-scrollbar-thumb {
  border-radius: 20px;
  background-color: #ad21ff;
}

*::-webkit-scrollbar-thumb:hover {
  background-color: #6200ff;
}

*::-webkit-scrollbar-thumb:active {
  background-color: #6200ff;
}

.navbar {
  max-width: 133rem;
  width: 100%;
  height: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1rem;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: transparent;
  z-index: 99999;
  margin: 0 auto;

}

.navbar-blur {
  backdrop-filter: blur(5px);
  background-color: rgba(255, 255, 255, 0.8);

}

@media (min-width: 1024px) {
  .navbar {
    max-width: 100%;
  }
}

.navbar_img {
  width: 14.5rem;
}

.navbar_img img {
  width: 100%;
  height: 100%;
}

.navbar_links {
  display: flex;
  list-style: none;
  gap: 2.1rem;
}

.navbar_links a {
  font-size: 1.6rem;
  font-family: "Rubik", sans-serif;
  font-weight: 500;
  color: #010103;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s;
}

.navbar_links a:hover {
  color: #ad21ff;
}

@media (max-width: 1000px) {
  .navbar_links {
    display: none;
  }
}

.navbar_buttons {
  display: flex;
  gap: 2.5rem;
  font-size: 1.6rem;
  font-family: "Rubik", sans-serif;
  font-weight: 500;
  align-items: center;
}

@media (max-width: 1000px) {
  .navbar_buttons {
    display: none;
  }
}

.navbar_buttons_sign-in {
  color: #000000;
  cursor: pointer;
  transition: all 0.3s;
}

.navbar_buttons_sign-in:hover {
  color: #ad21ff;
}

.navbar_buttons_register {
  background-color: #ad21ff;
  color: white;
  padding: 1.5rem 3rem;
  border-radius: 3px;
  box-shadow: 0 10px 15px 0 rgba(144, 24, 255, 0.5);
  transition: all 0.3s;
}

.navbar_buttons_register:hover {
  box-shadow: 0 10px 15px 0 rgba(144, 24, 255, 0.5);
  background-color: #6200ff;
}

.navbar_buttons a {
  text-decoration: none;
}

.mobile-hamb {
  font-size: 2.8rem;
  display: none;
  cursor: pointer;
  transition: all 0.3s;
}

.mobile-hamb:hover {
  color: #ad21ff;
}

@media (max-width: 1000px) {
  .mobile-hamb {
    display: flex;
  }
}

.mobile-navbar {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: -100%;
  background-color: #ffffff;
  z-index: 999999;
  justify-content: center;
  align-items: center;
  transition: all 0.5s ease-in-out;
}

.mobile-navbar_close {
  font-size: 3rem;
  position: absolute;
  top: 3.5rem;
  right: 3.5rem;
  cursor: pointer;
  transition: all 0.3s;
}

.mobile-navbar_close:hover {
  color: #ad21ff;
}

.mobile-navbar_links {
  display: flex;
  flex-direction: column;
  list-style: none;
  font-size: 2.3rem;
  gap: 3rem;
  text-align: center;
}

.mobile-navbar_links li a {
  text-decoration: none;
  color: #010103;
  font-weight: 500;
  transition: all 0.3s;
}

.mobile-navbar_links li a:hover {
  color: #ad21ff;
}

.open-nav {
  left: 0;
}

.hero-section {
  width: 100%;
  height: 97vh;
  background-color: #f8f8f8;
  position: relative;
}

.bg-shape {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
}

@media (max-width: 800px) {
  .bg-shape {
    display: none;
  }
}

.hero-content {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  position: relative;
}

@media (max-width: 800px) {
  .hero-content {
    justify-content: center;
  }
}

.hero-content_text {
  display: flex;
  flex-direction: column;
  z-index: 3;
  max-width: 50rem;
  margin-top: 5rem;
}

@media (max-width: 800px) {
  .hero-content_text {
    text-align: center;
    align-items: center;
  }
}

.hero-content_text h4 {
  font-size: 2.2rem;
  font-family: "Rubik", sans-serif;
  color: #010103;
}

.hero-content_text h1 {
  font-size: 5.2rem;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  color: #010103;
  line-height: 1.2;
  margin-top: 1rem;
  margin-bottom: 2.3rem;
}

.hero-content_text h1 span {
  color: #ad21ff;
}

.hero-content_text p {
  font-size: 1.6rem;
  font-family: "Rubik", sans-serif;
  line-height: 1.6;
  color: #706f7b;
  margin-bottom: 4rem;
}

.hero-content_text_btns {
  display: flex;
  gap: 2rem;
  font-size: 1.6rem;
  font-family: "Rubik", sans-serif;
}

@media (max-width: 450px) {
  .hero-content_text_btns {
    flex-direction: column;
  }
}

.hero-content_text_btns a {
  text-decoration: none;
  color: white;
  font-weight: 700;
}

.hero-content_text_btns_book-ride {
  background-color: #ad21ff;
  padding: 1.8rem 3rem;
  border-radius: 0.3rem;
  box-shadow: 0 10px 15px 0 rgba(144, 24, 255, 0.5);
  transition: all 0.3s;
  border: 2px solid #ad21ff;
}

@media (max-width: 450px) {
  .hero-content_text_btns_book-ride {
    padding: 1.8rem 8rem;
  }
}

.hero-content_text_btns_book-ride:hover {
  box-shadow: 0 10px 15px 0 rgba(130, 0, 252, 0.5);
  background-color: #6200ff;
}

.hero-content_text_btns_learn-more {
  background-color: #010103;
  padding: 1.8rem 3rem;
  border-radius: 0.3rem;
  border: 2px solid #010103;
  transition: all 0.3s;
}

.hero-content_text_btns_learn-more:hover {
  background-color: transparent;
  color: #010103;
}

.hero-content_car-img {
  z-index: 2;
  position: absolute;
  right: 0;
  width: 65%;
  margin-top: 5rem;
}

@media (max-width: 800px) {
  .hero-content_car-img {
    display: none;
  }
}

.scroll-up {
  position: fixed;
  font-size: 2.5rem;
  color: white;
  background-color: #ad21ff;
  border: 3px solid white;
  width: 2rem;
  height: 2rem;
  bottom: 5rem;
  right: 5rem;
  z-index: 20;
  display: none;
  align-items: center;
  text-align: center;
  justify-content: center;
  padding: 2rem;
  cursor: pointer;
}

.show-scroll {
  display: flex;
}

.book-section {
  position: relative;
  background: linear-gradient(to bottom, #f8f8f8 20%, #ffffff 80%);
}

.book-content {
  margin: 0 auto;
  margin-bottom: 10rem;
}

.book-content_box {
  background-color: #ffffff;
  width: 100%;
  padding: 4rem 4.5rem 5rem 5.5rem;
  box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1);
  height: auto;
  position: relative;
  z-index: 4;
  color: white;
  border-radius: 5px;
  background-image: url("/src/images/book-car/book-bg.png");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  color: #010103;
}

@media (max-width: 400px) {
  .book-content_box {
    padding: 4rem 2.5rem 5rem 2.5rem;
  }
}

.book-content_box h2 {
  font-size: 2.4rem;
  font-weight: 700;
  margin-bottom: 2.7rem;
}

.book-content_box .box-form {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto auto auto;
  gap: 2rem;
}

.book-content_box .box-form b {
  color: #ad21ff;
}

@media (max-width: 1000px) {
  .book-content_box .box-form {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 630px) {
  .book-content_box .box-form {
    grid-template-columns: 1fr;
  }
}

.book-content_box .box-form_car-type {
  display: flex;
  flex-direction: column;
}

.book-content_box .box-form_car-type label {
  font-size: 1.6rem;
  font-weight: 600;
  margin-bottom: 1.2rem;
}

.book-content_box .box-form_car-type label i {
  color: #ad21ff;
}

.book-content_box .box-form_car-type select {
  font-size: 1.5rem;
  color: #ababab;
  font-family: "Rubik", sans-serif;
  border: 1px solid #ccd7e6;
  border-radius: 3px;
  font-weight: 400;
  padding: 1.2rem 1.3rem;
  outline: none;
}

.book-content_box .box-form_car-time {
  display: flex;
  flex-direction: column;
  position: relative;
}

.book-content_box .box-form_car-time input {
  outline: none;
  color: #878585;
  padding-right: 3rem;
  border: 1px solid #ccd7e6;
}

.book-content_box .box-form_car-time label {
  font-size: 1.6rem;
  font-weight: 600;
  margin-bottom: 1.2rem;
}

.book-content_box .box-form_car-time label i {
  color: #ad21ff;
}

.book-content_box .box-form_car-time ::-webkit-datetime-edit {
  padding: 1.3rem 1.3rem;
}

.book-content_box .box-form_car-time ::-webkit-calendar-picker-indicator {
  cursor: pointer;
  font-size: 1.6rem;
}

.book-content_box button {
  padding: 1.3rem 1.3rem;
  height: -moz-fit-content;
  height: fit-content;
  align-self: self-end;
  border: none;
  font-family: "Rubik", sans-serif;
  font-size: 1.8rem;
  font-weight: 500;
  color: white;
  background-color: #ad21ff;
  box-shadow: 0 10px 15px rgba(144, 24, 255, 0.5);
  cursor: pointer;
  transition: all 0.3s;
}

.book-content_box button:hover {
  box-shadow: 0 10px 15px rgb(119, 0, 255);
}

.error-message {
  color: #721c24;
  background-color: #f8d7da;
  border-color: #f5c6cb;
  padding: 1rem 1.4rem;
  margin-bottom: 1.8rem;
  margin-top: -1rem;
  border: 1px solid transparent;
  border-radius: 0.8rem;
  font-size: 1.6rem;
  font-weight: 500;
  display: none;
  justify-content: space-between;
  align-items: center;
}

.error-message i {
  cursor: pointer;
}

.booking-done {
  color: #2a6817;
  background-color: #c3fabe;
  border-color: #f5c6cb;
  padding: 1rem 1.4rem;
  margin-bottom: 1.8rem;
  margin-top: -1rem;
  border: 1px solid transparent;
  border-radius: 0.8rem;
  font-size: 1.6rem;
  font-weight: 500;
  display: none;
  justify-content: space-between;
  align-items: center;
}

.booking-done i {
  cursor: pointer;
}

.active-modal {
  opacity: 1 !important;
  display: flex !important;
}

.modal-overlay {
  opacity: 0;
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 999999999;
  top: 0;
  right: 0;
}

.booking-modal {
  opacity: 0;
  display: none;
  flex-direction: column;
  position: fixed;
  overflow-x: hidden;
  overflow-y: scroll;
  z-index: 999999999999;
  top: 54%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 83rem;
  height: 100vh;
  border: 2px solid white;
  background-color: rgb(255, 255, 255);
  padding-right: 2px;
  color: #010103;
}

@media (max-width: 800px) {
  .booking-modal {
    width: 100%;
  }
}

@media (max-width: 600px) {
  .booking-modal {
    top: 50%;
  }
}

.booking-modal_title {
  display: flex;
  justify-content: space-between;
  padding: 1rem 1.5rem;
  background-color: #ad21ff;
  color: white;
  align-items: center;
}

.booking-modal_title h2 {
  font-size: 2.4rem;
  text-transform: uppercase;
}

.booking-modal_title i {
  font-size: 2.5rem;
  color: rgba(255, 255, 255, 0.919);
  cursor: pointer;
  transition: all 0.2;
}

.booking-modal_title i:hover {
  color: white;
}

.booking-modal_message {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  padding: 2rem 3rem;
  background-color: #ffeae6;
}

.booking-modal_message h4 {
  font-size: 1.9rem;
  font-weight: 700;
  color: #ad21ff;
}

.booking-modal_message h4 i {
  font-size: 2.5rem;
}

.booking-modal_message p {
  font-size: 1.7rem;
  color: #777777;
  font-weight: 500;
  line-height: 1.6;
}

.booking-modal_car-info {
  background-color: white;
  padding: 3rem 3rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  border-bottom: 1px solid rgba(119, 119, 119, 0.6235294118);
}

@media (max-width: 650px) {
  .booking-modal_car-info {
    grid-template-columns: 1fr;
    text-align: center;
  }
}

.booking-modal_car-info_dates {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.booking-modal_car-info_dates h5 {
  font-size: 1.8rem;
  color: #ad21ff;
}

.booking-modal_car-info_dates span {
  display: flex;
  gap: 1rem;
}

@media (max-width: 650px) {
  .booking-modal_car-info_dates span {
    grid-template-columns: 1fr;
    text-align: center;
    justify-content: center;
  }
}

.booking-modal_car-info_dates span i {
  font-size: 1.8rem;
  padding-top: 0.2rem;
  color: #777777;
}

@media (max-width: 650px) {
  .booking-modal_car-info_dates span i {
    display: none;
  }
}

.booking-modal_car-info_dates span h6 {
  font-size: 1.5rem;
  margin-bottom: 0.2rem;
}

.booking-modal_car-info_dates span p {
  font-size: 1.6rem;
  color: #777777;
}

.booking-modal_car-info_model {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

@media (max-width: 650px) {
  .booking-modal_car-info_model {
    margin-top: 3.5rem;
  }
}

.booking-modal_car-info_model h5 {
  font-size: 1.8rem;
  color: #ad21ff;
}

.booking-modal_car-info_model h5 span {
  color: #010103;
}

.booking-modal_car-info_model img {
  width: 100%;
  height: auto;
}

.booking-modal_person-info {
  padding: 3rem 3rem;
  background-color: white;
  display: flex;
  flex-direction: column;
}

.booking-modal_person-info h4 {
  font-size: 1.8rem;
  text-transform: uppercase;
  color: #ad21ff;
  margin-bottom: 2rem;
}

.input-time {
  text-align: center;
  width: 8rem;
  font-size: 14px;
  cursor: pointer;
}

.info-form {
  display: flex;
  flex-direction: column;
}

.info-form_checkbox {
  display: flex;
  gap: 1rem;
  align-items: center;
  font-size: 1.6rem;
  color: #565454;
  margin-top: 2rem;
  margin-bottom: 3rem;
}

.info-form_checkbox input {
  width: 1.6rem;
  height: 1.6rem;
}

.info-form_1col {
  grid-template-columns: 1fr !important;
}

.info-form_2col, .info-form_1col {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  padding: 1rem 0;
}

