.pick-car {
  width: 50rem;
  position: relative;
}

@media (max-width: 700px) {
  .pick-car {
    width: 100%;
  }
}

.pick-car img {
  width: 100%;
  margin-top: 6rem;
}

.pick-description {
  width: 25rem;
}

.pick-description_price {
  width: 100%;
  background-color: #ad21ff;
  font-size: 1.8rem;
  display: flex;
  align-items: center;
