.models-div_box_descr_name-price_details {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 2rem;
  -moz-column-gap: 7rem;
  column-gap: 7rem;
  margin-top: 2.5rem;
  margin: 2rem auto;
  padding-bottom: 2.5rem;
  border-bottom: 1px solid #c6c6c6;
}

.models-div_box_descr_name-price_details span {
  font-size: 1.8rem;
  font-weight: 500;
  color: #777777;
  text-align: left;
}

.models-div_box_descr_name-price_details span i {
  color: #010103;
}

.models-div_box_descr_name-price_btn {
  background-color: #ad21ff;
  padding: 1.8rem 3rem;
  border-radius: 0.3rem;
  box-shadow: 0 10px 15px 0 rgba(144, 24, 255, 0.5);
  transition: all 0.3s;
  border: 2px solid #ad21ff;
  font-size: 1.8rem;
  cursor: pointer;
}

.models-div_box_descr_name-price_btn:hover {
  box-shadow: 0 10px 15px 0 rgba(144, 24, 255, 0.5);
  background-color: #6200ff;
}

.models-div_box_descr_name-price_btn a {
  text-decoration: none;
  color: white;
  font-weight: 700;
}

.team-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto;
  gap: 4rem;
  align-items: center;
  text-align: center;
  padding: 10rem 2rem;
  width: 110rem;
  margin: 0 auto;
}

@media (max-width: 1150px) {
  .team-container {
    grid-template-columns: 1fr 1fr;
    width: -moz-fit-content;
    width: fit-content;
  }
}

@media (max-width: 800px) {
  .team-container {
    grid-template-columns: 1fr;
    width: -moz-fit-content;
    width: fit-content;
  }
}

.team-container_box {
  width: 33rem;
  background-color: white;
  box-shadow: 0px 20px 10px 0px rgba(0, 0, 0, 0.08);
}

@media (max-width: 400px) {
  .team-container_box {
    width: 100%;
  }
}

.team-container_box_img-div {
  width: 100%;
  height: auto;
  background-color: #f6f6f6;
}

.team-container_box_img-div img {
  width: 100%;
}

.team-container_box_descr {
  color: #010103;
  padding: 3rem;
}

.team-container_box_descr h3 {
  font-size: 2.2rem;
}

.team-container_box_descr p {
  font-size: 1.6rem;
  font-weight: 500;
  color: #777777;
}

.contact-div {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  margin: 0 auto;
  color: #010103;
  padding: 10rem 2rem;
  background-image: url("/src/images/banners/bg-contact.png");
  background-size: auto;
  background-position: center center;
  background-repeat: no-repeat;
}

@media (max-width: 950px) {
  .contact-div {
    grid-template-columns: 1fr;
    text-align: center;
  }
}

.contact-div_text {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  max-width: 41rem;
}

@media (max-width: 950px) {
  .contact-div_text {
    margin: 0 auto;
    margin-bottom: 2rem;
  }
}

.contact-div_text h2 {
  font-size: 4.2rem;
  line-height: 1.3;
  margin-bottom: 2rem;
}

.contact-div_text p {
  font-size: 1.6rem;
  font-family: "Rubik", sans-serif;
  color: #706f7b;
  line-height: 1.6;
  margin-bottom: 2rem;
}

.contact-div_text a {
  text-decoration: none;
  color: #010103;
  font-size: 1.6rem;
  font-weight: 500;
  transition: all 0.2s;
  margin-bottom: 0.5rem;
}

.contact-div_text a:hover {
  color: #ad21ff;
}

.contact-div_form {
  display: flex;
  flex-direction: column;
}

.contact-div_form form {
  display: flex;
  flex-direction: column;
}

.contact-div_form form label {
  font-size: 1.6rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

.contact-div_form form label b {
  color: #ad21ff;
}

.contact-div_form form input {
  background-color: #f2f2f2;
  padding: 19px 30px 19px 30px;
  font-size: 1.6rem;
  border: none;
  outline: none;
  margin-bottom: 2.3rem;
}

.contact-div_form form textarea {
  background-color: #f2f2f2;
  height: 18rem;
  padding: 19px 30px 19px 30px;
  font-size: 1.6rem;
  border: none;
  outline: none;
  margin-bottom: 2.5rem;
}

.contact-div_form form button {
  background-color: #ad21ff;
  padding: 1.8rem 3rem;
  border-radius: 0.3rem;
  box-shadow: 0 10px 15px 0 rgba(144, 24, 255, 0.5);
  transition: all 0.3s;
  border: 2px solid #ad21ff;
  color: white;
  font-size: 1.8rem;
  font-weight: 600;
  cursor: pointer;
}

.contact-div_form form button:hover {
  box-shadow: 0 10px 15px 0 rgba(144, 24, 255, 0.5);
  background-color: #6200ff;
}

